"use client"

// Firm categories with representative firms
const firmCategories = [
  {
    label: "Bulge Bracket",
    firms: ["Goldman Sachs", "Morgan Stanley", "J.P. Morgan", "Bank of America", "Citi"]
  },
  {
    label: "Elite Boutique", 
    firms: ["Lazard", "Evercore", "Centerview", "PJT Partners", "Moelis"]
  },
  {
    label: "Private Equity",
    firms: ["KKR", "Blackstone", "Apollo", "Carlyle", "TPG"]
  },
  {
    label: "Hedge Funds",
    firms: ["Citadel", "Point72", "Millennium", "D.E. Shaw", "Two Sigma"]
  }
]

export function FirmLogos() {
  return (
    <section className="relative bg-off-white py-16 overflow-hidden border-y border-charcoal/5">
      {/* Subtle background */}
      <div className="absolute inset-0 bg-gradient-to-r from-cream via-off-white to-cream" />
      
      <div className="relative mx-auto max-w-7xl px-6 lg:px-8">
        {/* Header */}
        <div className="text-center mb-10">
          <p className="text-xs font-bold uppercase tracking-[0.2em] text-charcoal/40">
            Our frameworks have generated interviews at
          </p>
        </div>
        
        {/* Scrolling logos */}
        <div className="relative">
          {/* Gradient masks */}
          <div className="absolute left-0 top-0 bottom-0 w-20 bg-gradient-to-r from-off-white to-transparent z-10 pointer-events-none" />
          <div className="absolute right-0 top-0 bottom-0 w-20 bg-gradient-to-l from-off-white to-transparent z-10 pointer-events-none" />
          
          {/* Scrolling row */}
          <div className="flex animate-scroll-slow overflow-hidden">
            {[...firmCategories, ...firmCategories, ...firmCategories].map((category, catIdx) => (
              category.firms.map((firm, firmIdx) => (
                <div
                  key={`${catIdx}-${firmIdx}`}
                  className="flex-shrink-0 px-8 py-3"
                >
                  <div className="flex items-center gap-3 px-5 py-3 rounded-xl bg-white/50 border border-charcoal/5 hover:border-gold/30 hover:bg-gold/5 transition-all duration-300 group">
                    {/* Firm initial as logo placeholder */}
                    <div className="w-8 h-8 rounded-lg bg-navy/5 flex items-center justify-center group-hover:bg-gold/10 transition-colors">
                      <span className="text-sm font-bold text-navy/60 group-hover:text-gold transition-colors">
                        {firm.split(' ').map(w => w[0]).join('').slice(0, 2)}
                      </span>
                    </div>
                    <span className="text-sm font-medium text-charcoal/60 group-hover:text-navy transition-colors whitespace-nowrap">
                      {firm}
                    </span>
                  </div>
                </div>
              ))
            )).flat()}
          </div>
        </div>
        
        {/* Stats row */}
        <div className="mt-10 flex flex-wrap items-center justify-center gap-8 text-center">
          {[
            { value: "30+", label: "Firms" },
            { value: "IB â€¢ PE â€¢ HF", label: "All Sectors" },
            { value: "100%", label: "Real Invitations" },
          ].map((stat, i) => (
            <div key={i} className="flex items-center gap-3">
              <span className="text-lg font-bold text-gold">{stat.value}</span>
              <span className="text-xs text-charcoal/40 uppercase tracking-wider">{stat.label}</span>
              {i < 2 && <span className="text-charcoal/20 ml-4">|</span>}
            </div>
          ))}
        </div>
      </div>
    </section>
  )
}
