function EmailPreview({ firm, division, type, tier, index }: { firm: string; division: string; type: string; tier: string; index: number }) {
  const colors = tierColors[tier] || tierColors.BB

  return (
    <div className="group relative flex-shrink-0 w-[340px] rounded-2xl border border-white/10 bg-gradient-to-br from-white/[0.08] to-white/[0.02] backdrop-blur-md transition-all duration-500 hover:border-gold/40 hover:scale-[1.02] hover:shadow-xl hover:shadow-gold/10 overflow-hidden">
      {/* Top accent line */}
      <div className={`h-1 w-full ${colors.bg} opacity-60`} />
      
      {/* Email content */}
      <div className="p-5">
        {/* Header */}
        <div className="flex items-start justify-between gap-3">
          <div className="flex items-center gap-3">
            <div className={`flex h-12 w-12 items-center justify-center rounded-xl ${colors.bg} ${colors.border} border`}>
              <Building2 className={`h-6 w-6 ${colors.text}`} />
            </div>
            <div>
              <div className="flex items-center gap-2">
                <span className="font-bold text-white text-lg">{firm}</span>
                <Verified className="h-4 w-4 text-gold flex-shrink-0" />
              </div>
              <p className="text-xs text-white/40">HR Department</p>
            </div>
          </div>
          <span className={`px-2 py-1 rounded-md text-[10px] font-bold ${colors.bg} ${colors.text} ${colors.border} border`}>
            {tierLabels[tier]}
          </span>
        </div>

        {/* Email preview */}
        <div className="mt-4 rounded-xl bg-navy/50 border border-white/5 p-4">
          <div className="flex items-center gap-2 text-xs text-white/30 mb-2">
            <Mail className="h-3.5 w-3.5" />
            <span>SUBJECT</span>
          </div>
          <p className="font-semibold text-white text-sm">
            {type} Interview Invitation - {division}
          </p>
          <div className="mt-3 pt-3 border-t border-white/5">
            <p className="text-xs text-white/50 leading-relaxed line-clamp-2">
              Dear [Name Redacted], We are pleased to invite you to interview for the {type} position in our {division} group. Your background and experience align well with...
            </p>
          </div>
        </div>

        {/* Footer */}
        <div className="mt-4 flex items-center justify-between">
          <div className="flex items-center gap-2 text-xs text-white/30">
            <span className="flex items-center gap-1">
              <span className="w-1.5 h-1.5 rounded-full bg-emerald-400" />
              Verified
            </span>
            <span>â€¢</span>
            <span>Name Redacted</span>
          </div>
          <div className="flex items-center gap-1 text-xs text-gold/60 group-hover:text-gold transition-colors">
            <span>Real Invite</span>
            <ExternalLink className="h-3 w-3" />
          </div>
        </div>
      </div>
      
      {/* Hover shine effect */}
      <div className="absolute inset-0 bg-gradient-to-r from-transparent via-white/5 to-transparent -translate-x-full group-hover:translate-x-full transition-transform duration-1000 pointer-events-none" />
    </div>
  )
}

export function ProofGallery() {
  return (
    <section className="relative bg-navy-deep py-24 lg:py-32 overflow-hidden">
      {/* Background effects */}
      <div className="absolute inset-0">
        <div className="absolute inset-0 bg-gradient-to-b from-off-white via-navy-deep to-navy-deep" />
        <div className="absolute inset-0 bg-[radial-gradient(ellipse_100%_50%_at_50%_0%,_rgba(201,168,110,0.15),_transparent)]" />
        <div className="absolute inset-0 bg-[radial-gradient(ellipse_80%_80%_at_80%_80%,_rgba(201,168,110,0.08),_transparent)]" />
      </div>
      
      {/* Grid pattern */}
      <div 
        className="absolute inset-0 opacity-[0.02]"
        style={{
          backgroundImage: `url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 30h60M30 0v60' stroke='%23ffffff' stroke-width='0.5'/%3E%3C/svg%3E")`,
        }}
      />

      <div className="relative">
        {/* Section header */}
        <div className="mx-auto max-w-7xl px-6 lg:px-8 text-center mb-16">
          <div className="inline-flex items-center gap-3 rounded-full border border-gold/30 bg-gold/10 px-5 py-2 mb-6">
            <span className="relative flex h-2 w-2">
              <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-emerald-400 opacity-75"></span>
              <span className="relative inline-flex rounded-full h-2 w-2 bg-emerald-400"></span>
            </span>
            <span className="text-xs font-bold uppercase tracking-[0.15em] text-gold">Real Results</span>
          </div>
          
          <h2 className="text-4xl sm:text-5xl lg:text-6xl font-bold tracking-tight text-white leading-tight">
            Interview Invitations from<br />
            <span className="text-gradient-gold">30+ Elite Firms</span>
          </h2>
          
          <p className="mt-6 text-lg text-white/50 max-w-2xl mx-auto">
            Our framework has generated interviews across every tier of financeâ€”
            from bulge brackets to elite boutiques to mega-fund PE.
          </p>
          
          {/* Tier legend */}
          <div className="mt-8 flex flex-wrap items-center justify-center gap-3">
            {Object.entries(tierLabels).map(([key, label]) => (
              <span 
                key={key} 
                className={`px-3 py-1.5 rounded-full text-xs font-medium ${tierColors[key].bg} ${tierColors[key].text} ${tierColors[key].border} border`}
              >
                {label}
              </span>
            ))}
          </div>
        </div>

        {/* Scrolling gallery */}
        <div className="relative mt-12">
          {/* Gradient masks */}
          <div className="absolute left-0 top-0 bottom-0 w-32 bg-gradient-to-r from-navy-deep via-navy-deep/80 to-transparent z-10 pointer-events-none" />
          <div className="absolute right-0 top-0 bottom-0 w-32 bg-gradient-to-l from-navy-deep via-navy-deep/80 to-transparent z-10 pointer-events-none" />

          {/* First row - scrolling left */}
          <div className="flex gap-5 mb-5 animate-scroll-left">
            {[...interviewInvites.slice(0, 15), ...interviewInvites.slice(0, 15)].map((invite, index) => (
              <EmailPreview key={`row1-${index}`} {...invite} index={index} />
            ))}
          </div>

          {/* Second row - scrolling right */}
          <div className="flex gap-5 animate-scroll-right">
            {[...interviewInvites.slice(15), ...interviewInvites.slice(15)].map((invite, index) => (
              <EmailPreview key={`row2-${index}`} {...invite} index={index + 15} />
            ))}
          </div>
        </div>

        {/* Bottom CTA */}
        <div className="mt-16 text-center px-6">
          <div className="inline-flex flex-col sm:flex-row items-center gap-6">
            <p className="text-white/40 text-sm">
              Ready to add your name to this list?
            </p>
            <a
              href="/submit-resume"
              className="group inline-flex items-center gap-2 rounded-2xl bg-gold px-8 py-4 text-base font-bold text-navy transition-all hover:bg-white hover:shadow-xl hover:shadow-gold/20 hover:scale-105"
            >
              Get Your Resume Reviewed
              <span className="transition-transform group-hover:translate-x-1">â†’</span>
            </a>
          </div>
        </div>
      </div>
    </section>
  )
}
